<script>
    import {AU_EMAIL, AU_EMAIL_STATUS, AU_SELECTED_SERVICE} from "../requestServer";
    import {notification} from "ant-design-vue";
    export default {
        name: "ManageBill",
        data() {
            return {
                isLoading: true,
                isPc: this.$isPc(),
                sortType: '',
                sortBy: '',
                showCancelDialog: false,
                billList: [],
                status: [],
                statusList: [{type: '全部', ischecked: false}, {
                    type: '开启',
                    status: 'Active',
                    ischecked: false
                }, {type: '停用', status: 'Inactive', ischecked: false}],
                payMethods: [],
                payMethodsList: [{key: '全部', ischecked: false}, {
                    key: '支付寶',
                    value: 'alipay',
                    ischecked: false
                }, {key: '微信支付', value: 'wepay', ischecked: false},
                    {key: '银联卡', value: 'union', ischecked: false}, {key: '信用卡', value: 'visa', ischecked: false},
                    {key: 'PayPal', value: 'paypal', ischecked: false}, {key: '加密貨幣', value: 'coin', ischecked: false}],
                payStatus: [],
                payStatusList: [{key: '全部', ischecked: false}, {
                    key: '已支付',
                    status: 'paid',
                    ischecked: false
                }, {key: '待支付', status: 'unpaid', ischecked: false}],
                showMsg: false,
                selectedItem: {},
                trafficArray: [{trafficLimit: '∞', speedLimit: '∞', isAddon: false, sessionLimit: '∞'}],
                speedArray: ['速度', '∞', '', '', '', ''],
                currentTime: new Date().getTime(),
                lang: this.$i18n.locale,
                loading: true,
                selectedService: localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {},
                settingsInfo: {},
                iconLoading: false,
                selectedId: 0,
                renewTimer: 0,
            }
        },
        created() {
            if (JSON.stringify(this.selectedService) === '{}') {
                this.getService();
            } else {
                this.isLoading = false;
                this.getMoreData();
            }
        },
        mounted() {
            this.selectedId = this.$route.params.serviceId;
        },
        methods: {
            getMoreData() {
                this.getServiceInfo();
                this.getSettingInfo();
                const that = this;
                this.$request.auGetAllInvoices('', '', function (data) {
                    that.loading = false;
                    if (data && (data.status === 200 || data.status === 201)) {
                        that.billList = data.data;
                    }
                });
            },
            getSettingInfo() {
                let that = this;
                this.$request.getSettingInfo(function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        that.settingsInfo = data.data;
                    }
                });
            },
            getService() {
                const that = this
                this.$request.auGetAllServices('', '', function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        if (data.data.jjService && data.data.jjService.length > 0) {
                            if (that.selectedId !== 0) that.selectedService = data.data.jjService.find(item => String(item.id) === that.selectedId);
                            if (JSON.stringify(that.selectedService) === '{}') that.selectedService = data.data.jjService[0];
                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(that.selectedService));
                        } else if (data.data.unpaidService && data.data.unpaidService.length > 0) {
                            that.selectedService = data.data.unpaidService[0];
                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(that.selectedService));
                        }
                    }
                    that.isLoading = false;
                    that.getMoreData();
                });
            },
            getPlanTitle() {
                this.lang = this.$i18n.locale;
                if (this.selectedItem.planName) {
                    if (this.lang === 'zh-CN' && this.selectedItem.planName['zh-cn']) {
                        return this.selectedItem.planName['zh-cn'];
                    } else if (this.lang === 'zh-TW' && this.selectedItem.planName['zh-tw']) {
                        return this.selectedItem.planName['zh-tw'];
                    } else if (this.selectedItem.planName['en']) {
                        return this.selectedItem.planName['en'];
                    }
                }
                return '';
            },
            getExpireDate() {
                let regArray = this.selectedService.regdate.split('-');
                let year = parseInt(regArray[0]);
                let mon = parseInt(regArray[1]);
                let day = parseInt(regArray[2]);
                if (this.selectedService.billingcycle === 'Annually') {
                    year = parseInt(year) + 1;
                } else if (this.selectedService.billingcycle === 'Monthly') {
                    year = mon + 1 > 12 ? year + 1 : year;
                    mon = mon + 1 > 12 ? (mon - 11) : mon + 1;
                } else {
                    year = mon + 6 > 12 ? year + 1 : year;
                    mon = mon + 6 > 12 ? (mon - 11) : mon + 6;
                }
                return year + '-' + (mon >= 10 ? mon : '0' + mon )  + '-' + (day >= 10 ? day : '0' + day);
            },
            nextRenewDate() {
                let nextduedate = this.selectedService.nextduedate;
                let billingcycle = this.selectedService.billingcycle;
                return nextduedate + ' 至 ' + this.countValidEndDay(billingcycle === 'Monthly' ? 1 : billingcycle === 'Annually' ? 12 : 6, nextduedate);
            },
            countValidEndDay(mon, regDate) {
                let regYear = parseInt(regDate.split('-')[0]);
                let regMon = parseInt(regDate.split('-')[1]);
                let regDay = parseInt(regDate.split('-')[2]);

                let lastDay = new Date(2020, regMon + mon, 0).getDate();// 获取当月最大天，这里的月份不用加减1

                let nextYear = regMon + mon > 12 ? regYear + 1 : regYear;
                let nextMon = regMon + mon > 12 ? regMon + mon - 12 : regMon + mon;
                let nextLastDay = regDay > lastDay ? lastDay : regDay;

                return nextYear + '-' + (nextMon > 9 ? nextMon : '0' + nextMon) + '-' + (nextLastDay > 9 ? nextLastDay : '0' + nextLastDay);
            },
            checkExpired() {
                return this.selectedItem.expiredTime !== undefined && (parseInt(this.selectedItem.expiredTime) - this.currentTime <= 7 * 24 * 3600)
            },
            payOrCheckInvoice(item) {
                if (this.isPc) {
                    let routeUrl = this.$router.resolve({
                        path: 'user/invoice/:invoiceId',
                        name: 'invoiceDetail',
                        params: {
                            invoiceId: item.id,
                        },
                        query: this.$route.query
                    });
                    window.open(routeUrl.href, this.$inApp ? '_self' : '_blank');
                } else {
                    this.$router.push({
                        path: 'user/invoice/:invoiceId',
                        name: 'invoiceDetail',
                        params: {
                            invoiceId: item.id,
                        },
                        query: this.$route.query
                    });
                }
            },
            goToSupport() {
                window.open(this.settingsInfo.embedCode.CS_CODE, this.$inApp? '_self': '_blank');
            },
            goToRenew() {
                let relid = this.selectedService.status === 'Pending' ? this.selectedService.id : this.selectedService.relid;
                this.iconLoading = true;
                const that = this;
                this.$request.auGetLastInvoice(relid,function (data) {
                    that.iconLoading = false;
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        let routeUrl = that.$router.resolve({
                            path: 'order/pay',
                            name: 'payment',
                            params: {
                                isRenew: true,
                            },
                            query: {
                                invoiceId: data.data.id,
                                ...that.$route.query
                            }
                        });
                        window.open(routeUrl.href, that.$inApp? '_self': '_blank');
                        that.checkRenewStatus()
                    } else {
                        notification['warning']({
                            message: '消息提示',
                            description:
                            '生成續費帳單失敗，請聯繫客服。',
                        });
                    }
                });
            },
            checkRenewStatus() {
                if (this.renewTimer === 0) {
                    this.startPollingTime = new Date().getTime();
                    const that = this;
                    this.renewTimer = setInterval(() => {
                        this.$request.auGetAllServices('','',function (data) {
                            if (data && (data.status === 200 || data.status === 201) && data.data) {
                                localStorage.setItem(AU_EMAIL, data.data.email);
                                localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                                if (data.data.jjService && data.data.jjService.length > 0) {
                                    data.data.jjService.forEach(item => {
                                        let currenId = that.selectedService.status === 'Pending' ? that.selectedService.id : that.selectedService.relid;
                                        if (item.relid === currenId) {
                                            window.clearInterval(that.renewTimer);
                                            that.renewTimer = 0;
                                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(item));
                                            that.selectedService = item;
                                            that.getMoreData();
                                        }
                                    });
                                }
                            }
                        });
                        if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 3) {
                            window.clearInterval(that.renewTimer);
                            that.renewTimer = 0;
                        }
                    }, 3000);
                }
            },
            goToOrderProduct() {
                window.open(`${window.location.origin}/order/place`,this.$inApp? '_self': '_blank');
            },
            payInvoice(id) {
                this.$request.auPayInvoice(id, function (data) {
                    if (data && (data.status === 200 || data.status === 201)) {
                        window.open(data.data.redirectUrl, this.$inApp ? '_self' : '_blank');
                    }
                });
            },
            formatDate(value) {
                let date = new Date(value);
                let y = date.getFullYear();
                let MM = date.getMonth() + 1;
                MM = MM < 10 ? ('0' + MM) : MM;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                return y + '-' + MM + '-' + d;
            },
            payMethodsChange(item) {
                console.log(item)
            },
            payStatusChange(item) {
                console.log(item)
            },
            getServiceInfo() {
                this.trafficArray = [{trafficLimit: '0', speedLimit: 0, isAddon: false, sessionLimit: '∞'}];
                this.speedArray = ['速度', '', '', '', '', ''];
                let that = this;
                this.$request.getServiceInfo(function (data) {
                    if (data && data.status === 200) {
                        that.selectedItem = data.data;
                        let dashUrl = that.selectedItem.dashboardUrl;
                        that.selectedItem['usageDashboardUrl'] = dashUrl.substring(0, dashUrl.lastIndexOf("&token"));
                        that.selectedItem.email = that.selectedItem.email ? that.selectedItem.email.endsWith('@auvpn.net') && that.selectedItem.email.length > 40 ? '' : that.selectedItem.email : '';
                        that.selectedItem['speedDeviceLimit'] = [];
                        let speeds = [];
                        that.selectedItem.limitRuleDtos.sort((a, b) => {
                            return a.trafficLimit - b.trafficLimit;
                        });
                        const addon = that.selectedItem.addon / Math.pow(1024, 3);
                        if (addon > 0) {
                            that.trafficArray[0].isAddon = true;
                            that.trafficArray.push({
                                trafficLimit: addon,
                                speedLimit: 0,
                                isAddon: false,
                                sessionLimit: 0
                            });
                        }
                        if (that.selectedItem.limitRuleDtos.length > 0) {
                            that.selectedItem.limitRuleDtos.forEach((item, index) => {
                                item.trafficLimit /= Math.pow(1024, 3);
                                item.speedLimit = item.speedLimit / Math.pow(1024, 2) * 8;
                                item.trafficLimit += addon;
                                if (index === 0) {
                                    if (item.trafficLimit === 0) {
                                        that.trafficArray[0].speedLimit = item.speedLimit;
                                        that.trafficArray[0].sessionLimit = item.sessionLimit;
                                        speeds.push(item.speedLimit);
                                    } else {
                                        that.trafficArray.push(item);
                                        speeds.push(item.speedLimit);
                                        that.trafficArray[0].speedLimit = 'addon';
                                        if (addon > 0) that.trafficArray[1].speedLimit = 'addon';
                                    }
                                } else {
                                    that.trafficArray.push(item);
                                    speeds.push(item.speedLimit);
                                }
                            });
                            speeds.sort((a, b) => {
                                return b - a;
                            });
                            if (speeds.length > 0) {
                                if (speeds[0] > 0) {
                                    that.speedArray[1] = speeds[0];
                                    that.speedArray[3] = speeds[0] / 2;
                                } else {
                                    that.speedArray[1] = '∞';
                                }
                            }
                        } else {
                            that.trafficArray[0].speedLimit = '∞';
                            if (addon > 0) that.trafficArray[1].speedLimit = '∞';
                            that.speedArray[1] = '∞';
                            if (that.trafficArray.length === 1) that.trafficArray[0].trafficLimit = '∞';
                        }
                        //确认当前适合的规则
                        const totalUsage = (parseFloat(that.selectedItem.trafficDn + that.selectedItem.trafficUp) / Math.pow(1024, 3)).toFixed(2);
                        that.trafficArray.forEach((item, index) => {
                            if (index !== that.trafficArray.length - 1) {
                                if (totalUsage >= parseFloat(item.trafficLimit) && totalUsage < parseFloat(that.trafficArray[index + 1].trafficLimit)) {
                                    that.selectedItem['speedDeviceLimit'].push(item);
                                }
                            } else {
                                if (totalUsage >= parseFloat(item.trafficLimit)) {
                                    that.selectedItem['speedDeviceLimit'].push(item);
                                }
                            }
                        });
                        if (that.selectedItem['speedDeviceLimit'].length === 0) {
                            that.selectedItem['speedDeviceLimit'].push(that.trafficArray[that.trafficArray.length - 1]);
                        }
                    } else {
                        that.infoError = true;
                        if (data && data.status === 502) {
                            that.apiDevelopError = true;
                        }
                    }
                });
            },
            fraudOrder() {
                //const that = this;
                this.$request.auFraudOrder('', '', function (data) {
                    console.log(data);
                });
            }
        }
    }
</script>

